import axios from 'axios'
import {BASE_URL} from './common'
// import qs from 'querystring'
let base = BASE_URL

let AUTH_TOKEN = sessionStorage.getItem('access-user')
axios.defaults.headers.common['Authorization'] = AUTH_TOKEN
/**
 * 同步方式获取用户相关关键词
 * @param params
 */
export const getKeywords = params => {
  return axios.get(`${base}/keywords/findByType`, {params: params}).then(res => res.data)
}
